import React,{useEffect, useState} from 'react'
import 'bootstrap/dist/css/bootstrap.min.css';
import '../components/Style/Style.css';
import {useParams} from 'react-router-dom';
import ProductsLoading from '../components/HomeComponents/SecondSection/ProductsLoading';
import {Link} from 'react-router-dom';
import SingleLoading from '../components/SingleComponents/SingleLoading';
import {connect} from 'react-redux';
import {addProductToCart} from "../actions/cartActions";
import * as products from '../components/Data/products.json';
import toast, { Toaster } from 'react-hot-toast';

function SingleProduct(props) {
    const { id } = useParams();
    const [product, setProducts] =useState(['']);
    const [count, setCount] = useState(1);
    const [loading, setLoading] = useState(true);

    useEffect(() => {
            fetch('https://fakestoreapi.com/products')
            .then(res=>res.json())
            .then(json=> {setProducts(json);setLoading(false)});
    }, [])

    const message = (productMap) => {
        if(props.state.shop.items.some(element => element.id == id)){
            return toast.error('Product is already to Cart');
        }
        else{
            return (
                toast.success('Product added to Cart Succesfully'),
                props.addProductToCart(productMap)
            )
        }
    }

    return (
        <div className="single">
            <div className="product-main">
                <div className="container">
                    {
                        !loading ? products.data.filter(pr => pr.id == id).map(productMap => (
                            <div className="product">
                                <div className="product-image" >
                                    <img src={productMap.image}/>
                                    <div class="images-view-wrap">
                                        <ul class="images-view-list">
                                            <li class="active">
                                                <div class="images-view-item">
                                                    <img src={productMap.image}/>
                                                </div>
                                            </li>
                                        </ul>
                                    </div>
                                </div>
                                <div className="product-content">
                                    <div className="product-title">
                                        <h3>{productMap.description}</h3>
                                    </div>
                                    <div className="product-price">
                                        <h3>US {productMap.price}$</h3>
                                    </div>
                                    <div className="product-quantity">
                                        <div className='title'>
                                            Quantity:
                                        </div>
                                        <div className='buttons'>
                                            <input type='submit' onClick={() => setCount(count - 1)} value='-'/>
                                            <p>{count}</p>
                                            <input type='submit' onClick={() => setCount(count + 1)} value='+'/>
                                        </div>
                                    </div>
                                    <div className="product-shipping">
                                        <div className="product-shipping-price">
                                            <span className="bold">Free Shipping</span>
                                        </div>
                                        <span className="product-shipping-info black-link">
                                            to Albania via AliExpress Standard Shipping
                                            <i className="next-icon next-icon-arrow-down next-xxs"></i>
                                        </span>
                                        <span className="product-shipping-date">
                                            <span className="product-shipping-delivery">Estimated Delivery: <span>26-43</span> days</span>
                                            <span className="shipping-tips-icon-wrap">
                                            </span>
                                        </span>
                                    </div>
                                    <div className="product-action">
                                        <button className='by-now'>Buy Now</button>
                                        <button className='add-cart' onClick={(e) => {message(productMap)}}>Add to Cart</button>
                                    </div>
                                    <div className="buyer-pretection">
                                        <div className="buyer-pretection-wrap clearfix">
                                            <a className="buyer-pretection-info clearfix" target="_blank" rel="noopener noreferrer" href="https://sale.aliexpress.com/v8Yr8f629D.htm#origin">
                                                <div className="buyer-pretection-icon">
                                                    <svg className="icon-svg" style={{fontSize: 30}}>
                                                        <svg id="icon-BP" viewBox="0 0 1024 1024"><path d="M523.67187473 190.36718759a45.23437519 45.23437519 0 0 0-21.23437446 5.32031259L284.65625009 312.17187527a46.26562473 46.26562473 0 0 0-24.37499982 41.01562436V504.96875027c-3.91406232 109.45312537 38.85937509 253.24218731 263.39062446 325.9453126 224.53125018-72.70312527 267.35156269-216.51562518 263.43750036-324.98437528v-152.76562509c0-17.22656277-9.32812509-32.95312509-24.37499982-40.96875009l-217.80468814-116.48437509a45.32812537 45.32812537 0 0 0-21.2578124-5.34374973z m0 696.63281232l-7.73437446-2.41406268C238.78906241 798.453125 203.79687537 609.14843731 207.57031241 504.03125009v-150.84375046a99.84374973 99.84374973 0 0 1 52.50000036-88.3828125l217.82812446-116.48437426a98.34375009 98.34375009 0 0 1 91.57031295 0l217.85156241 116.48437426a99.84374973 99.84374973 0 0 1 52.49999954 88.3828125V504.96875027c3.796875 104.17968787-31.21875 293.48437473-308.43749926 379.61718696l-7.73437528 2.41406268z"></path><path d="M488.96093741 654.28906231l-152.06249954-154.50000018 37.26562445-37.87499981 114.79687509 116.62500037 184.21874972-187.1484376 37.26562528 37.87499982z"></path></svg>
                                                    </svg>
                                                </div>
                                                <div className="buyer-pretection-content">
                                                    <div className="buyer-pretection-title bold">75-Day Buyer Protection</div>
                                                    <div className="buyer-pretection-context">Money back guarantee</div>
                                                </div>
                                            </a>
                                        </div>
                                    </div>
                                </div>
                                <div className="product-fix-sns">
                                    <div className="product-fix-wrap">
                                        <div>
                                            <div className="sns-shares">
                                                <span data-spm="share" className="ui-fixed-panel-unit ui-fixed-panel-facebook">
                                                    <a href="https://www.facebook.com/sharer/sharer.php?u=https%3A%2F%2Fwww.aliexpress.com%2Fitem%2F1005002381804881.html%3Fchannel%3Dfacebook" target="_blank"></a>
                                                </span>
                                            <span data-spm="share" className="ui-fixed-panel-unit ui-fixed-panel-pinterest">
                                                <a href="https://www.pinterest.com/pin/create/button/?url=https%3A%2F%2Fwww.aliexpress.com%2Fitem%2F1005002381804881.html%3Fchannel%3Dpinterest&amp;media=https%3A%2F%2Fae01.alicdn.com%2Fkf%2FH340ace01811e4b5c9c6bd6ce58a1a60ev%2FIntel-Core-i9-7900X-ES-QLRX-Ten-Core-Twenty-Thread-CPU-Processor-13-75M-LGA-2066.jpg&amp;description=Cheap%20CPUs%2C%20Buy%20Quality%20Computer%20%26%20Office%20Directly%20from%20China%20Suppliers%3AIntel%20Core%20i9%207900X%20ES%20QLRX%20Ten%20Core%20Twenty%20Thread%20CPU%20Processor%2013.75M%20LGA%202066%20Need%20MSI%20ASROCK%20GIGABYTE%20X299%20Motherboard%0AEnjoy%20%E2%9C%93Free%20Shipping%20Worldwide!%20%E2%9C%93Limited%20Time%20Sale%C2%A0%E2%9C%93Easy%20Return." target="_blank"></a>
                                            </span>
                                            <span data-spm="share" className="ui-fixed-panel-unit ui-fixed-panel-vk">
                                                <a href="https://vk.com/share.php?url=https%3A%2F%2Fwww.aliexpress.com%2Fitem%2F1005002381804881.html%3Fchannel%3Dvk&amp;title=91.16%E2%82%AC%2025%25%20OFF%7CIntel%20Core%20i9%207900X%20ES%20QLRX%20Ten%20Core%20Twenty%20Thread%20CPU%20Processor%2013.75M%20LGA%202066%20Need%20MSI%20ASROCK%20GIGABYTE%20X299%20Motherboard%7CCPUs%7C%20%20%20-%20AliExpress&amp;image=https%3A%2F%2Fae01.alicdn.com%2Fkf%2FH340ace01811e4b5c9c6bd6ce58a1a60ev%2FIntel-Core-i9-7900X-ES-QLRX-Ten-Core-Twenty-Thread-CPU-Processor-13-75M-LGA-2066.jpg" target="_blank"></a>
                                            </span>
                                            <span data-spm="share" className="ui-fixed-panel-unit ui-fixed-panel-twitter">
                                                <a href="https://twitter.com/intent/tweet?url=https%3A%2F%2Fwww.aliexpress.com%2Fitem%2F1005002381804881.html%3Fchannel%3Dtwinner&amp;text=91.16%E2%82%AC%2025%25%20OFF%7CIntel%20Core%20i9%207900X%20ES%20QLRX%20Ten%20Core%20Twenty%20Thread%20CPU%20Processor%2013.75M%20LGA%202066%20Need%20MSI%20ASROC..." target="_blank"></a>
                                            </span>
                                        </div>
                                        <div className="sns-secondary">
                                            <span className="ui-fixed-panel-unit ui-fixed-panel-survey">
                                                <a href="//home.aliexpress.com/survey.htm?fromPage=aedetail" target="_blank"></a>
                                            </span>
                                        </div>
                                    </div>
                                </div>
                                </div>
                            </div>
                        ))
                        :<SingleLoading/>
                    }
                    <Toaster position='top-right' toastOptions={{style: {marginTop: '40%'},success: {duration: 4000}}}/>
                </div>
                <div className='second-section-single'>
                    <div className='container'>
                        <div className='may-like'>
                            <div className='title'>
                                <h2>You may Like</h2>
                            </div>
                            <ul className="products">
                                {   
                                    loading ? <ProductsLoading/>
                                    :
                                    product.map(item => (
                                        <li className="product-item">
                                            <Link to={"/product/" + item.id}>
                                                <div className="product-image">
                                                    <img src={item.image} />
                                                </div>
                                                <div className="product-content">
                                                    <h5 className="product-title">{item.title}</h5>
                                                    <h4 className="product-price">{item.price}$</h4>
                                                </div>
                                            </Link>
                                        </li>
                                    ))
                                }
                            </ul>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
}
  
const mapStateToProps = state => ({
    state
});
  
  export default connect(mapStateToProps,{addProductToCart})(SingleProduct);
